.app{}

.app__contol-panel{
    margin-bottom: 20px;
}
.app-heading{
    margin: 20px 0;
}
.search-panel{
    margin-right: 10px;
}
.todo-list{
    margin-bottom: 20px;
}

.todo-list__item{
    padding:0.375rem 0.75rem;
}

.todo-list__item .is-done{
    display: block;
    position: relative;
}

.todo-list__item .is-done::after{
    display: block;
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    top: calc( 50% + 1px );
    background: var(--danger);
    z-index: 1;
}
.todo-list-item{}

.todo-list-item__text{
    cursor: pointer;
}

.todo-list-item__btns button{
    width: 40px;
}
